<template>
  <div class="pager">
    <el-pagination
      v-model:current-page="currentPage"
      v-model:page-size="pageSize"
      :page-sizes="[5, 10, 20, 50, 100, 200, 500]"
      :background="background"
      layout="total, sizes, prev, pager, next"
      :total="theCountItemsTotal"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
    />
  </div>
</template>

<script>
export default {
  name: "settings-projects-pager",

  computed: {
    items() {
      return [];
    },

    theCountItemsTotal() {
      return this.$store.getters["project/total"];
    },

    pageSize() {
      return this.$store.getters["project/pageSize"];
    },

    currentPage() {
      return this.$store.getters["project/currentPage"];
    },
  },

  data() {
    return {
      background: true,
      // pageSize: 1,
      // currentPage: 1,
    };
  },

  methods: {
    handleCurrentChange(value) {
      let index = value - 1;
      this.$store.commit("project/update", { index });
    },

    handleSizeChange(value) {
      let size = value;
      let index = 0;
      this.$store.commit("project/update", { size, index });
    },
  },
};
</script>

<style scoped>
.pager {
  margin-top: 60px;
  margin-bottom: 20px;
}
</style>
