<template>
  <div>
    <el-table :data="items">
      <!-- <el-table-column prop="id" label="ID"></el-table-column> -->

      <el-table-column prop="name" label="名称" sortable>
        <template #default="scope">
          <div :title="scope.row.path">{{ scope.row.name }}</div>
        </template>
      </el-table-column>

      <el-table-column
        prop="project_type_name"
        label="类型"
        sortable
      ></el-table-column>

      <!-- <el-table-column prop="path" label="路径" sortable></el-table-column> -->

      <el-table-column label="状态" prop="state">
        <template #default="scope">
          <FileStateView :code="scope.row.state" />
        </template>
      </el-table-column>

      <el-table-column label="操作">
        <template #default="scope">
          <el-button link type="primary">详情</el-button>
          <el-button link type="primary" @click="handleClickEditItem(scope.row)"
            >编辑</el-button
          >
          <el-button
            link
            type="danger"
            @click="handleClickDeleteItem(scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <MyEditDialog
      v-model="displayDialog"
      title="编辑工程项目"
      :target="targetItem"
      method="PUT"
    />
  </div>
</template>

<script>
import FileStateView from "@/components/utils/filestate.vue";
import MyEditDialog from "./edit-dialog.vue";
import { ElMessageBox } from "element-plus";

export default {
  name: "settings-projects-table",

  components: { MyEditDialog, FileStateView },

  computed: {
    items() {
      return this.$store.getters["project/listCurrentPage"];
    },

    currentPageIds() {
      let src = this.items;
      let dst = [];
      if (src == null) {
        return dst;
      }
      for (var i in src) {
        let item = src[i];
        dst.push(item.id);
      }
      return dst;
    },

    currentPageIdstr() {
      let src = this.currentPageIds;
      let str = "";
      for (var i in src) {
        let id = src[i];
        str += id + ".";
      }
      return str;
    },
  },

  data() {
    return {
      displayDialog: false,
      targetItem: {},
    };
  },

  methods: {
    formatItemState() {
      return "ready"; // todo ...
    },

    handleClickEditItem(item) {
      this.displayDialog = true;
      this.targetItem = item;
    },

    handleClickDeleteItem(item) {
      let title = "移除工程";
      let text = "确定要移除 [" + item.name + "] 这个工程吗？";
      let desc =
        "该操作不会把磁盘上的工程文件删除，仅将对它的引用从WPM数据库中移除。";
      ElMessageBox.confirm(text + desc, title, {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.removeItem(item);
        })
        .catch(() => {});
    },

    removeItem(item) {
      this.$store.dispatch("project/remove", item);
    },

    fetchByIds(ids) {
      this.$store.dispatch("project/fetchByIds", { ids });
    },
  },

  watch: {
    currentPageIdstr() {
      this.fetchByIds(this.currentPageIds);
    },
  },
};
</script>
