<template>
  <div>
    <el-select
      :model-value="modelValue"
      @update:modelValue="fireUpdateModelValue"
      placeholder="请选择"
    >
      <el-option
        v-for="item in options"
        :key="item.id"
        :label="computeLabel(item)"
        :value="item.id"
      ></el-option>

      <el-option label="未定义" :value="0"></el-option>
    </el-select>

    <el-button type="default" title="刷新" @click="refresh">
      <el-icon> <Refresh /> </el-icon>
    </el-button>
  </div>
</template>

<script setup>
import { Refresh } from "@element-plus/icons-vue";
</script>

<script>
export default {
  name: "settings-project-types-options-selector",

  computed: {
    options() {
      return this.$store.getters["project_type/all"];
    },
  },

  data() {
    return {
    };
  },

  methods: {
    refresh() {
      this.$store.dispatch("project_type/fetch");
    },

    computeLabel(item) {
      let s1 = item.label;
      let s2 = item.name;
      return s1 + " (" + s2 + ")";
    },

    fireUpdateModelValue(value) {
      this.$emit("update:modelValue", value);
    },
  },

  props: {
    modelValue: Number,
  },
};
</script>
