<template>
  <div class="search-box">
    <el-input
      :modelValue="searchtext"
      @input="applyFilter"
      clearable
      placeholder="请输入要查找的内容"
    >
      <template #append>
        <el-button title="查找">
          <el-icon> <Search /> </el-icon>
        </el-button>
      </template>
    </el-input>
  </div>
</template>

<script setup>
import { Search } from "@element-plus/icons-vue";
</script>

<script>
export default {
  name: "settings-projects-search",

  computed: {
    searchtext() {
      return this.$store.getters["project/filterText"];
    },
  },

  methods: {
    applyFilter(text) {
      this.$store.commit("project/setFilter", { text });
    },
  },
};
</script>

<style scoped>
.search-box {
  max-width: 380px;
}
</style>
