
<template>
  <div class="toolbar">
    <el-button @click="handleClickRefresh" title="刷新">
      <el-icon><Refresh /></el-icon>
    </el-button>

    <el-button @click="handleClickAdd" title="添加本地仓库">
      <el-icon><Plus /></el-icon>
    </el-button>
  </div>
</template>
  
<script setup>
import { Refresh, Plus } from "@element-plus/icons-vue";
</script>
 
<script>
export default {
  name: "settings-projects-toolbar",

  methods: {
    handleClickAdd() {
      const name = "insert";
      this.$emit("action", { name });
    },

    handleClickRefresh() {
      this.$store.dispatch("project/fetch", { all: 1 });
    },
  },
};
</script>

<style scoped>
.toolbar {
  display: flex;
  flex-direction: row;
  justify-content: right;
}
</style>
