<template>
  <div class="settings-project-page">
    <h1>工程项目</h1>

    <MyToolbar @action="handleAction" />

    <MySearch class="search-box" />

    <MyPager class="pager-box" />

    <MyTable @action="handleAction" />

    <MyInsertDialog
      v-model="displayInsertDialog"
      :target="theInsertDialogTarget"
      title="导入工程项目"
      method="POST"
    />
  </div>
</template>

<script>
import MyToolbar from "./toolbar";
import MySearch from "./search";
import MyPager from "./pager";
import MyTable from "./table";
import MyInsertDialog from "./edit-dialog.vue";

export default {
  name: "settings-projects",
  components: { MyToolbar, MySearch, MyPager, MyTable, MyInsertDialog },

  data() {
    return {
      displayInsertDialog: false,
      theInsertDialogTarget: {},
    };
  },

  methods: {
    handleAction(p) {
      let name = p.name;
      if (name == "refresh") {
        this.refresh();
      } else if (name == "insert") {
        this.insert();
      } else {
        console.warn("unsupported action:" + name);
      }
    },

    insert() {
      this.theInsertDialogTarget = {};
      this.displayInsertDialog = true;
    },

    refresh() {
      this.$store.dispatch("project/fetch", { all: 1 });
    },
  },

  mounted() {
    this.refresh();
  },
};
</script>

<style scoped>
.settings-project-page {
  text-align: left;
}
</style>
