<template>
  <el-dialog>
    <el-form>
      <el-form-item label="名称">
        <el-input v-model="current.name"></el-input>
      </el-form-item>

      <!-- <el-form-item label="标题">
        <el-input v-model="current.title"></el-input>
      </el-form-item> -->

      <el-form-item label="路径">
        <el-input v-model="current.path">
          <template #append>
            <el-button @click="handleClickBrowsePath"> 浏览 </el-button>
          </template>
        </el-input>
      </el-form-item>

      <el-form-item label="类型">
        <MyProjectTypeSelector v-model="current.project_type_id" />
      </el-form-item>

      <el-form-item label="描述">
        <el-input v-model="current.description" type="textarea"></el-input>
      </el-form-item>
    </el-form>

    <div>
      <el-button type="success" @click="save">保存</el-button>
      <el-button type="default" @click="close"> 取消 </el-button>
    </div>

    <OpenFileDialog
      v-model="displayOpenFileDialog"
      @open-file="handleOpenExeFiles"
    />
  </el-dialog>
</template>

<script>
import OpenFileDialog from "@/components/filebrowser/dialog-open.vue";
import MyProjectTypeSelector from "@/components/settings/content-types/options-selector.vue";

export default {
  name: "settings-projects-edit-dialog",

  components: { OpenFileDialog, MyProjectTypeSelector },

  data() {
    return {
      current: {},
      displayOpenFileDialog: false,
    };
  },

  methods: {
    handleClickBrowsePath() {
      this.displayOpenFileDialog = true;
    },

    handleOpenExeFiles(items) {
      if (items == null) {
        return;
      }
      let item = items[0];
      if (item == null) {
        return;
      }
      let path = item.path;
      if (path == null) {
        return;
      }
      this.current.path = path;
    },

    getActionName() {
      let m = this.method;
      if (m == "POST") {
        return "insert";
      } else if (m == "PUT") {
        return "update";
      }
      return "no_action";
    },

    save() {
      let item = this.current;
      let action = this.getActionName();
      this.$store.dispatch("project/" + action, { item }).then(() => {
        this.close();
      });
    },

    close() {
      this.$emit("update:modelValue", false);
    },

    cloneObject(o) {
      let str = JSON.stringify(o);
      return JSON.parse(str);
    },
  },

  props: {
    method: String,
    target: Object,
  },

  watch: {
    target(value) {
      this.current = this.cloneObject(value);
    },
  },
};
</script>
